class Row extends React.Component{
    /*constructor(props){
        super(props)
    }*/
    render(){
        let orders = [
            {
                selected: false,
                num: 1,
                name: 'Ordine 1',
                date: '25/12/20',
                state: true,
                tot: 300
            },
            {
                selected: false,
                num: 2,
                name: 'Ordine 2',
                date: '25/12/20',
                state: false,
                tot: 250
            },
            {
                selected: false,
                num: 3,
                name: 'Ordine 3',
                date: '25/12/20',
                state: true,
                tot: 600
            },
        ]
        return(
            <>
                {
                    orders.map((order,i)=>{
                        return <div className="row" key={"order-"+i}>
                                <div className="col-3 row">
                                    <div className="col-4 text-center">
                                        <input type="checkbox" id="studentCheckAll" value={order.selected} />
                                    <label htmlFor={"studentCheck-"+i} className="pt-1"></label>
                                    </div>
                                    <div className="col-8">
                                        {order.num}
                                    </div>
                                </div>
                                <div className="col">
                                    {order.name}
                                </div>
                                <div className="col">
                                    {order.date}
                                </div>
                                <div className="col-2 text-center text-white">
                                    {order.state?
                                        <div className="bg-success rounded">
                                            Completo
                                        </div>
                                        :
                                        <div className="bg-danger rounded">
                                            Incompleto
                                        </div>
                                    }
                                </div>
                                <div className="col">
                                    {order.tot}
                                </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </>
        )
    }
}
