import React from 'react'
import autoBind from "react-autobind"
import { Link } from "react-router-dom";
import Navbar from "./Navbar"
import firebase from "./Firestone"


export default class UpdateCourse extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            action: this.props.match.params.action,
            id: this.props.match.params.id,
            course:{
                nome: '',
                introduzione: '',
                descrizione: '',
                stato: '',
                docente: '',
                sku: '',
                prezzo: '',
                categoria: '',
                immagine: '',
                abilitato: true,
                iva: 0
            },
            categories:[]
        }
        if(this.props.match.params.id!=='0'){
            this.getCouseData(this.props.match.params.id)
        }
        this.getCategories()
        autoBind(this)
    }
    async getCategories(){
        let categories = []
        const db = firebase.firestore();
        await db.collection("categorie").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                categories.push({id: doc.id, ...doc.data()})
            });
        });
        this.setState({categories})
    }
    async getCouseData(id){
        console.log('get')
        const db = firebase.firestore();
        let course = null
        await db.collection("corsi").doc(id).get().then((querySnapshot) => {
            course = {...querySnapshot.data()}
        });
        this.setState({course: {...this.state.course, ...course}})
        console.log(this.state.course)
    }
    sendData(){
        const db = firebase.firestore();
        console.log(this.state.course)
        if(this.state.id!=='0'){
            db.collection("corsi").doc(this.state.id).set({
                ...this.state.course
            })
            .catch(function(error) {
                alert(error)
            })
        }
        else{
            db.collection("corsi").add({
                ...this.state.course
            })
            .catch(function(error) {
                alert(error)
            })
        }
        this.goBack()
    }
    goBack(){
        this.props.history.push('/courses')
    }
    render(){
        return(
            <>
                <Navbar/>
                <div className="container">
                    <div className="row">
                        <div className="col-11">
                            <div className="h1 my-4 float-left">
                                Corso
                            </div>
                        </div>
                        <div className="col-1">
                            <Link to="/courses" className="float-right my-4 p-4">
                                <img src={require('../App/img/icon/close.png')} className="w-100" alt="" />
                            </Link>
                        </div>
                    </div>
                    <div className="row mx-0 mt-4">
                        <span className=" font-weight-bold fontS18">Dettagli</span>
                        <hr className="col-12"/>
                            <div className="col-6">
                                <p className="fontS14 mb-2">
                                    Nome
                                </p>
                                <input type="text"
                                    value={this.state.course.nome}
                                    onChange={(e)=>this.setState({course:{...this.state.course,nome: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-6">
                                <p className="fontS14 mb-2">
                                    Introduzione
                                </p>
                                <input type="text"
                                    value={this.state.course.introduzione}
                                    onChange={(e)=>this.setState({course:{...this.state.course,introduzione: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-12">
                                <p className="fontS14 mb-2">
                                    Descrizione
                                </p>
                                <textarea type="text"
                                    value={this.state.course.descrizione}
                                    onChange={(e)=>this.setState({course:{...this.state.course,descrizione: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-3">
                                <p className="fontS14 mb-2">
                                    Abilitato
                                </p>
                                <select
                                        value={this.state.course.abilitato}
                                        onChange={(e)=>this.setState({course:{...this.state.course,abilitato: e.target.value}})}
                                    className="h48 rounded fontS14 textBlueDeep"
                                    >
                                    <option value={'true'}>Abilitato</option>
                                    <option value={'false'}>Disabilitato</option>
                                </select>
                            </div>
                            <div className="col-3">
                                <p className="fontS14 mb-2">
                                    Docente
                                </p>
                                <input type="text"
                                    value={this.state.course.docente}
                                    onChange={(e)=>this.setState({course:{...this.state.course,docente: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-3">
                                <p className="fontS14 mb-2">
                                    Sku
                                </p>
                                <input type="text"
                                    value={this.state.course.sku}
                                    onChange={(e)=>this.setState({course:{...this.state.course,sku: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-2">
                                <p className="fontS14 mb-2">
                                    Prezzo (IVA Esclusa)
                                </p>
                                <input type="text"
                                    value={this.state.course.prezzo}
                                    onChange={(e)=>this.setState({course:{...this.state.course,prezzo: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-1">
                                <p className="fontS14 mb-2">
                                    IVA
                                </p>
                                <select
                                    value={this.state.course.iva}
                                    onChange={(e)=>this.setState({course:{...this.state.course,iva: e.target.value}})}
                                    className="h48 rounded fontS14 textBlueDeep"
                                    >
                                    <option value={0}>IVA</option>
                                    <option value={1}>Esente</option>
                                </select>
                            </div>
                            <div className="col-6">
                                <p className="fontS14 mb-2">
                                    Categoria
                                </p>
                                <select
                                    value={this.state.course.categoria}
                                    onChange={(e)=>this.setState({course:{...this.state.course,categoria: e.target.value}})}
                                    className="h48 rounded fontS14 textBlueDeep"
                                    >
                                    <option value="" disabled selected hidden>Scegliere Categoria</option>
                                    {
                                        this.state.categories.map((category,i)=>{
                                            return <option value={category.id} key={"categoria-"+i}>{category.nome}</option>
                                        })
                                    }
                                </select>
                            </div>
                            <div className="col-6">
                                <p className="fontS14 mb-2">
                                    Immagine
                                </p>
                                <input type="text"
                                    value={this.state.course.immagine}
                                    onChange={(e)=>this.setState({course:{...this.state.course,immagine: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-9"></div>
                            <div className="col-3">
                                <button className="btn btn-success h48 w-100 mt-3" onClick={this.sendData}>
                                    Salva
                                </button>
                            </div>
                    </div>
                </div>
            </>
        )
    }
}
