import React from 'react';
import autoBind from "react-autobind";
import { Link, withRouter } from "react-router-dom";
import conf from "../../conf/conf"
import { Modal, ModalHeader, ModalBody } from 'reactstrap';
import { AppCtxStore } from "../App/App.context";

class Login extends React.Component {
    static contextType = AppCtxStore;

    constructor(props) {

        super(props);

        this.state = {
            email: '',
            password: '',
            showPassword: 'password',
            activationResponse: null,
            activationMessage: '',
            modalState: false,
        };

        this.inputEmail = {};
        this.inputPassword = {};

        autoBind(this);
    }

    togglePassword() {
        if (this.state.showPassword === 'password') {
            this.setState({
                showPassword: 'text',
            });
        }
        else {
            this.setState({
                showPassword: 'password',
            });
        }
    }

    async loginUser(email, password) {
        /*localStorage.setItem("user", JSON.stringify(res.data.login));
            await this.context.setUser(res.data.login);
            console.log(res.data.login)
            this.props.isLogged(1)*/
    }

    async componentDidMount() {
        let id = parseInt(this.props.match.params.id)
        let token = this.props.match.params.token //activationToken
        let recoveryToken = this.props.match.params.recoveryToken //recoveryToken 
        console.log("RECOVERY TOKEN", recoveryToken)
        console.log("ACTIVATION TOKEN", token)

        if (id)
            return;

        if (token) {
            var resp = await fetch(conf.apiUrl + "/activation/" + id + "/" + token);
            var values = await resp.json();
            this.setState({ activationResponse: values.response, activationMessage: values.message });

        } else if (recoveryToken) {
            var recoveryResp = await fetch(conf.apiUrl + "/pass_recover/" + this.props.match.params.email + "/" + recoveryToken);
            var recoveryValue = await recoveryResp.json();
            console.log("RECOVERY VALUE", recoveryValue)
            this.setState({ recoveryResponse: recoveryValue.response, recoveryMessage: recoveryValue.message });

            this.toggleModal()

        }
    }

    toggleModal() {
        this.setState(prevState => ({
            modalState: !prevState.modalState
        }));
    }


    render() {
        return (
            
            <div style={{'minHeight':'670px','height':'calc(100vh - 287px)'}} className="row mx-0 loginPageContainer">
                <div className="col-4"></div>
                <div className="col-4 my-auto">
                    <div style={{'maxWidth':'500px'}} className="bgWhite rounded p-5 loginContainer mx-auto">
                        {this.state.activationResponse &&
                            <div className="my-0 fontS24 text-success" >{this.state.activationMessage}</div>}

                            <div className="text-center">
                                <p className="my-0 fontS24 textBlueDeep">
                                    Login into your account
                            </p>
                                <p className="fontS13 textGreyLite d-inline-block">Don't have an account? <Link to="/registration" className="fontS13 textBlueLight d-inline-block">Create New</Link></p>
                            </div>
                            <div className="mt-4 px-5">
                                <div className="position-relative">
                                    <input type="email" className="h48 w-100 mb-4 rounded" ref={node => { this.inputEmail = node }} />
                                    <img className="inputRightIcon" src={require("../App/img/icon/email.png")} alt="" />

                                    <input type={this.state.showPassword} ref={node => { this.inputPassword = node }} className="h48 w-100 mb-4 rounded" />
                                    <img className="inputRightIcon" src={require("../App/img/icon/eyeGrey.png")} onClick={this.togglePassword} alt="Show Password" />
                                </div>
                                <div className="bgPurpleLite buttonImg w-100 pt-2 rounded text-center h48 fontS14 textWhite" onClick={(e) => {
                                    this.loginUser(this.inputEmail.value, this.inputPassword.value)
                                }}>
                                    <div className="pt-1">
                                        Login
                                </div>
                                </div>
                            </div>
                        
                <div>
                    <Modal isOpen={this.state.modalState} toggle={this.toggleModal} className={this.props.className}>
                        {this.state.recoveryResponse ? <ModalHeader toggle={this.toggleModal}>Check your Email!</ModalHeader>
                            : <ModalHeader toggle={this.toggleModal}>Be careful!</ModalHeader>}
                        <ModalBody>
                            {this.state.recoveryMessage}
                        </ModalBody>
                    </Modal>
                </div>
                    </div>
                </div>
                <div className="col-4"></div>
            </div>
        );
    }
}

export default withRouter(Login);
