import React from 'react';
import { Switch, Route } from 'react-router';
import { BrowserRouter as Router } from "react-router-dom";

import Home from "../Routes/Home"
import Students from "../Routes/Students"
import Student from "../Routes/Student"
import Login from "../Routes/Login"
import Update from "../Routes/UpdateStudent"
import conf from "../../conf/conf"
import Report from '../Routes/Reports';
import Orders from '../Routes/Orders';
import Categories from '../Routes/Categories';
import Courses from '../Routes/Courses';
import UpdateCourses from '../Routes/UpdateCourse';
import CourseModule from '../Routes/CourseModules';
import UpdateModule from '../Routes/UpdateModule'
import UpdateCategory from '../Routes/UpdateCategory'

/**
 * This function is used by our sitemap generator
 * DO NOT REMOVE this method, just change routes to your needs
 * if you want to exclude some routes from sitemap just
 * add them directly inside the <Router>
 */
export const routePaths = () => (
    <Switch>
        <Route exact path="/home" component={Home} />
        <Route exact path="/" component={Students} />
        <Route exact path="/Login" component={Login} />
        <Route exact path="/report" component={Report} />
        <Route exact path="/orders" component={Orders} />
        <Route exact path="/students" component={Students} />
        <Route exact path="/student/:id" component={Student} />
        <Route exact path="/student/:action/:id" component={Update} />
        <Route exact path="/courses" component={Courses} />
        <Route exact path="/courses/:action/:id" component={UpdateCourses} />
        <Route exact path="/modules/:id_course" component={CourseModule} />
        <Route exact path="/modules/:id_course/:action/:id" component={UpdateModule} />
        <Route exact path="/categories" component={Categories} />
        <Route exact path="/categories/:action/:id" component={UpdateCategory} />
    </Switch>
)

export default (props) => (
    <Router basename={conf.basePath}>
        <div>
            {props.children}
            {routePaths()}
        </div>
    </Router >
);
