import React from 'react'
import autoBind from "react-autobind"
import Navbar from "./Navbar"
import { Link } from "react-router-dom"
import firebase from "./Firestone"


export default class Categories extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            categories: []
        }
        this.getCategories()
        autoBind(this)
    }
    async getCategories(){
        let categories = []
        const db = firebase.firestore();
        await db.collection("categorie").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                categories.push({id: doc.id, ...doc.data()})
            });
        console.log(categories)
        });
        this.setState({categories})
    }
    render(){
        return(
            <>
            <Navbar />
            <div className="container">
                <div className="w-100 row">
                    <div className="col-9">
                        <div className="h1 my-4">
                            Categorie
                        </div>
                    </div>
                    <div className="col-3 px-0">
                        <Link to="/categories/update/0">
                            <button className="btn btn-success h48 wFit mt-4">
                                Nuova categoria
                            </button>
                        </Link>
                    </div>
                </div>
                <div className="row font-weight-bold mt-4">
                    <hr className="col-12"/>
                    <div className="col">
                        Nome
                    </div>
                    <div className="col-2 pl-4">
                        Abilitato
                    </div>
                    <div className="col">
                        
                    </div>
                    <hr className="col-12 mt-1"/>
                </div>
                {
                    this.state.categories.length
                        ?<Row categories={this.state.categories}/>
                        :"Caricamento..."
                }
            </div>
            </>
        )
    }
}

class Row extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            categories: this.props.categories
        }
        autoBind(this)
    }
    async getCategories(){
        let categories = []
        const db = firebase.firestore();
        await db.collection("categorie").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                categories.push({id: doc.id, ...doc.data()})
            });
        console.log(categories)
        });
        this.setState({categories})
    }
    deleteCategories(id){
        const db = firebase.firestore();
        db.collection("categorie").doc(id).delete().then(function() {
            alert("Categoria Rimossa!")
        }).catch(function(error) {
            alert("Errore: ", error)
        })
        this.getCategories()
    }
    render(){
        return(
            <>
                {
                    this.state.categories.map((category,i)=>{
                        return <div className="row" key={"category-"+i}>
                            <div className="col">
                                {category.nome}
                            </div>
                            <div className="col-2 text-center text-white pl-1">
                                {category.abilitato === 'false'
                                    ?<div className="bg-danger rounded wFit px-2">
                                        Off-line
                                    </div>
                                    :<div className="bg-success rounded wFit px-2">
                                        On-line
                                    </div>
                                }
                            </div>
                            <div className="col row">
                                <Link to={"/categories/update/"+category.id} className="col bg-warning textWhite h30 rounded-left borderMedium shadowInsetBottom pointer text-center">
                                    Modifica
                                </Link>
                                <Link to="/" className="col bg-white h30 borderMedium shadowInsetBottom pointer text-center">
                                    Organizza
                                </Link>
                                <div className="col bg-danger textWhite h30 rounded-right borderMedium shadowInsetBottom pointer text-center" onClick={()=>this.deleteCategories(category.id)}>
                                    Elimina
                                </div>
                            </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </>
        )
    }
}
