import React from 'react'
import autoBind from "react-autobind"
import { Link } from "react-router-dom";
import Navbar from "./Navbar"
import firebase from "./Firestone"

export default class students extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            selectAll: 0,
            students: []
        }
        this.getStudents()
        autoBind(this)
    }
    async getStudents(){
        let students = []
        const db = firebase.firestore();
        await db.collection("utenti").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                students.push({id: doc.id, selezionato:false, ...doc.data()})
            });
        console.log(students)
        });
        this.setState({students})
    }
    toggleAll(){
        let selectAll = !this.state.selectAll
        console.log(selectAll)
        var _students = this.state.students
        _students.forEach(student => {
            student.selezionato = selectAll
        });
        this.setState({selectAll, students: _students})
    }
    deleteStudent(id){
        const db = firebase.firestore();
        db.collection("studenti").doc(id).delete().then(function() {
            alert("Utente Rimosso!")
        }).catch(function(error) {
            alert("Errore: ", error)
        })
        this.getStudents()
    }
    render(){
        return(
            <>
            <Navbar />
            <div className="container">
                <div className="h1 my-4">
                    Corsisti
                </div>
                <div className="row">
                    <div className="col-9">
                        <div className="w-100 row">
                            <div className="col-9 col-md-3 col-lg-3">
                                <input type="text" className="w-100 rounded h48"/>
                            </div>
                            <div className="col-3 col-md-1 col-lg-1 px-0">
                                <button className="btn btn-success h48 w-100">
                                    Cerca
                                </button>
                            </div>
                        </div>
                    </div>
                    <div className="col-3 px-0">
                        <Link to="/student/admin_new_student/0">
                            <button className="btn btn-success h48 wFit">
                                Nuovo Studente
                            </button>
                        </Link>
                    </div>
                </div>
                <div className="row font-weight-bold mt-4">
                    <hr className="col-12"/>
                    <div className="col-4 row">
                        <div className="col-2 text-center">
                            <input type="checkbox" id="studentCheckAll" value={this.state.selectAll} onClick={this.toggleAll}/>
                            <label htmlFor="studentCheckAll" className="pt-1"></label>
                        </div>
                        <div className="col-5">
                            Cognome
                        </div>
                        <div className="col-5">
                            Nome
                        </div>
                    </div>
                    <div className="col-3 row">
                        <div className="col-10">
                            Email
                        </div>
                        <div className="col-2 text-center">
                            Attivo
                        </div>
                    </div>
                    <div className="col-5 text-center">
                        Azioni
                    </div>
                    <hr className="col-12 mt-1"/>
                </div>
                {
                    this.state.students.length
                        ?<StudentsRow students={this.state.students} selectAll={this.state.selectAll}/>
                        :"Caricamento..."
                }
            </div>
            </>
        )
    }
}

class StudentsRow extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            students: this.props.students
        }
        autoBind(this)
    }
    checkS(i){
        let _students = this.state.students
        _students[i].selezionato = !_students[i].selezionato;
        this.setState({students: _students})
    }
    async getStudents(){
        let students = []
        const db = firebase.firestore();
        await db.collection("utenti").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                students.push({id: doc.id, selezionato:false, ...doc.data()})
            });
        console.log(students)
        });
        this.setState({students})
    }
    deleteStudent(id){
        const db = firebase.firestore();
        db.collection("utenti").doc(id).delete().then(function() {
            alert("Utente Rimosso!")
        }).catch(function(error) {
            alert("Errore: ", error)
        })
        this.getStudents()
    }
    render(){
        return(
            <>
                {
                    this.state.students.map((student,i)=>{
                        return <div className="row" key={"student-"+i}>
                            <div className="col-4 row">
                                <div className="col-2 text-center">
                                    <input type="checkbox" id={"studentCheck-"+i} defaultChecked={student.selezionato} onClick={()=>this.checkS(i)}/>
                                    <label htmlFor={"studentCheck-"+i} className="pt-1"></label>
                                </div>
                                <div className="col-5">
                                    {student.cognome}
                                </div>
                                <div className="col-5">
                                    {student.nome}
                                </div>
                            </div>
                            <div className="col-3 row">
                                <div className="col-10">
                                    {student.email}
                                </div>
                                <div className="col-2 text-center">
                                    {student.abilitato?<span className="text-success">Si</span>:<span className="text-danger">No</span>}
                                </div>
                            </div>
                            <div className="col-5 text-center row">
                                <div className="col-1"></div>
                                <Link to={"/student/"+student.id} className="col-3 bg-warning textWhite h30 rounded-left borderMedium shadowInsetBottom pointer">
                                    Monitoraggio
                                </Link>
                                <Link to={"/student/admin_update_student/"+student.id} className="col-3 bg-white h30 borderMedium shadowInsetBottom pointer">
                                    Modifica
                                </Link>
                                <div className="col-3 bg-success textWhite h30 borderMedium shadowInsetBottom pointer">
                                    Accedi come
                                </div>
                                <div className="col-2 bg-danger textWhite h30 rounded-right borderMedium shadowInsetBottom pointer" onClick={()=>this.deleteStudent(student.id)}>
                                    Elimina
                                </div>
                            </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </>
        )
    }
}
