import React from 'react'
import autoBind from "react-autobind"
import { Link } from "react-router-dom"
import Navbar from "./Navbar"
import firebase from "./Firestone"


export default class Student extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            id: this.props.match.params.id,
            student: [],
            studentCourses: [],
            manual: 0
        }
        this.getStudentData(this.props.match.params.id)
        this.getSudentCourses(this.props.match.params.id)
        autoBind(this)
    }
    async getStudentData(id){
        const db = firebase.firestore()
        let student = null
        await db.collection("utenti").doc(id).get().then((querySnapshot) => {
            student = {...querySnapshot.data()}
        })
        this.setState({student})
    }
    async getSudentCourses(id){
        const db = firebase.firestore()
        let studentCourses = []
        await db.collection("iscrizioni_corsi").where("id_studente", "==", id).get().then((querySnapshot) => {
            if(!(querySnapshot.empty)){
                console.log(querySnapshot)
                querySnapshot.forEach((doc) => {
                    studentCourses.push({id: doc.id, ...doc.data()})
                })
            }
        })
        this.setState({studentCourses:studentCourses})
    }
    render(){
        return(
            <>
                <Navbar/>
                <div className="container">
                <div className="row">
                    <div className="col-11">
                        <div className="h1 my-4 float-left">
                            {
                                this.state.student
                                    ?`${this.state.student.cognome} ${this.state.student.nome}`
                                    :'Caricamento...'
                            }
                        </div>
                    </div>
                    <div className="col-1">
                        <Link to="/students" className="float-right my-4 p-4">
                            <img src={require('../App/img/icon/close.png')} className="w-100" alt="" />
                        </Link>
                    </div>
                </div>
                <div className="row font-weight-bold mt-4">
                    <hr className="col-12"/>
                    <div className="col-7 row mx-0">
                        <div className="col-1 text-center">
                            ID
                        </div>
                        <div className="col-11">
                            Nome del corso
                        </div>
                    </div>
                    <div className="col-3 row mx-0">
                        <div className="col text-center">
                            Completato
                        </div>
                        <div className="col">
                            Punteggio
                        </div>
                    </div>
                    <div className="col-1">
                        Dettagli
                    </div>
                    <div className="col-1">
                        Abilitato
                    </div>
                    <hr className="col-12 mt-1"/>
                </div>
                {
                    this.state.studentCourses.length
                    ?<CoursesRow studentCourses={this.state.studentCourses} student_id={this.state.id}/>
                    :''
                }
                {
                    this.state.manuale
                        ?<AddManual student_id={this.state.id} studentCourses={this.state.studentCourses}/>
                        :<div className="w-100 text-center mt-3">
                            <button className="btn btn-success h48 wFit mt-3" onClick={()=>this.setState({manuale:true})}>
                                Gestione manuale
                            </button>
                        </div>
                }
            </div>
            </>
        )
    }
}

class AddManual extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            student_id: this.props.student_id,
            studentCourses: this.props.studentCourses.length?this.props.studentCourses:[],
            courses: []
        }
        this.getCourses()
        autoBind(this)
    }
    async getCourses(){
        let courses = []
        const db = firebase.firestore()
        await db.collection("corsi").where("abilitato","!=","false").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                courses.push({id: doc.id, selezionato:false, ...doc.data()})
            })
        })
        this.setState({courses})
    }
    checkCourse(course_id){
        for(let course of this.state.studentCourses){
            if(course.id_corso === course_id){
                return 1
            }
        }
        return 0
    }
    async getSudentCourses(id){
        const db = firebase.firestore()
        let studentCourses = []
        await db.collection("iscrizioni_corsi").where("id_studente", "==", id).get().then((querySnapshot) => {
            if(!(querySnapshot.empty)){
                querySnapshot.forEach((doc) => {
                    studentCourses.push({id: doc.id, ...doc.data()})
                })
                this.setState({studentCourses})
            }
        })
    }
    assign(id_course){
        let dataToSend = {
            id_corso: id_course,
            id_studente: this.state.student_id,
            stato: 'Attivo'
        }
        const db = firebase.firestore()
        db.collection("iscrizioni_corsi").add(dataToSend)
        .catch(function(error) {
            alert(error)
        })
        this.getSudentCourses(this.state.student_id)
    }
    remove(id_course){
        let i = 0
        let studentCourses = this.state.studentCourses
        for(let course of studentCourses){
            if(course.id_corso === id_course){
                const db = firebase.firestore()
                db.collection("iscrizioni_corsi").doc(course.id).delete().then(function() {
                    alert("Corso Rimosso!")
                }).catch(function(error) {
                    alert("Errore: ", error)
                })
                studentCourses.splice(i,1)
                this.setState({studentCourses:studentCourses})
            }
            i++
        }
    }
    render(){
        return(
            <div className="mt-5">
                <p className="fontS24 font-weight-bold">
                    Corsi Manuali
                </p>
                <hr className="w-100"/>
                {
                    this.state.courses.map((course,i)=>{
                        return <div className="row" key={"course-"+i}>
                            <div className="col-1">
                                {course.sku}
                            </div>
                            <div className="col">
                                {course.nome}
                            </div>
                            <div className="col-2 pl-4">
                                {course.prezzo}
                            </div>
                            <div className="col-2">
                                {
                                    this.checkCourse(course.id)
                                        ?<div className="bg-danger px-3 textWhite h30 rounded borderMedium shadowInsetBottom pointer text-center wFit" onClick={()=>this.remove(course.id)}>
                                            Rimuovi
                                        </div>
                                        :<div className="bg-success px-3 textWhite h30 rounded borderMedium shadowInsetBottom pointer text-center wFit" onClick={()=>this.assign(course.id)}>
                                            Assegna
                                        </div>
                                }
                            </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </div>
        )
    }
}
class CoursesRow extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            studentCourses: this.props.studentCourses,
            student_id: this.props.student_id,
            courses: [],
            dataToShow: []
        }
        this.getCourses()
        autoBind(this)
    }
    async getCourses(){
        let courses = []
        const db = firebase.firestore()
        await db.collection("corsi").where("abilitato","!=","false").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                courses.push({id: doc.id, selezionato:false, ...doc.data()})
            })
        })
        this.setState({courses})
        this.formatData();
    }
    formatData(){
        let dataToShow = []
        for(let studentCourse of this.state.studentCourses){
            for(let course of this.state.courses){
                if(studentCourse.id_corso === course.id){
                    dataToShow.push({...course, ...studentCourse})
                }
            }
        }
        this.setState({dataToShow:dataToShow})
    }
    render(){
        return(
            <>
                {
                    this.state.dataToShow.map((course,i)=>{
                        return(
                            <div className="row" key={"student-"+i}>
                                <div className="col-7 row mx-0">
                                    <div className="col-1 text-center">
                                        {course.sku}
                                    </div>
                                    <div className="col-11">
                                        {course.nome}
                                    </div>
                                </div>
                                <div className="col-3 row mx-0">
                                    <div className="col text-center text-white">
                                        {course.stato === 'false'
                                            ?<div className="bg-danger rounded">
                                                Incompleto
                                            </div>
                                            :<div className="bg-success rounded">
                                                Completo
                                            </div>
                                        }
                                    </div>
                                    <div className="col">
                                        {course.voto}
                                    </div>
                                </div>
                                <div className="col-1">
                                    Dettagli
                                </div>
                                <div className="col-1">
                                    {course.abilitato}
                                </div>
                                <hr className="col-12"/>
                            </div>
                        )
                    })
                }
            </>
        )
    }
}
