import React from 'react'
import autoBind from "react-autobind"
import { Link } from "react-router-dom";
import Navbar from "./Navbar"
import firebase from "./Firestone"


export default class UpdateCategory extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            action: this.props.match.params.action,
            id: this.props.match.params.id,
            category:{
                nome: '',
                descrizione: '',
                abilitato: true
            }
        }
        if(this.props.match.params.id!=='0'){
            this.getCategoryData(this.props.match.params.id)
        }
        autoBind(this)
    }
    async getCategoryData(id){
        console.log('get')
        const db = firebase.firestore();
        let category = null
        await db.collection("categorie").doc(id).get().then((querySnapshot) => {
            category = {...querySnapshot.data()}
        });
        this.setState({category: {...this.state.category, ...category}})
        console.log(this.state.category)
    }
    sendData(){
        const db = firebase.firestore();
        if(this.state.id!=='0'){
            db.collection("categorie").doc(this.state.id).set({
                ...this.state.category
            })
            .catch(function(error) {
                alert(error)
            })
        }
        else{
            db.collection("categorie").add({
                ...this.state.category
            })
            .catch(function(error) {
                alert(error)
            })
        }
        this.goBack()
    }
    goBack(){
        this.props.history.push('/categories')
    }
    render(){
        return(
            <>
                <Navbar/>
                <div className="container">
                    <div className="row">
                        <div className="col-11">
                            <div className="h1 my-4 float-left">
                                Categoria
                            </div>
                        </div>
                        <div className="col-1">
                            <Link to="/categories" className="float-right my-4 p-4">
                                <img src={require('../App/img/icon/close.png')} className="w-100" alt="" />
                            </Link>
                        </div>
                    </div>
                    <div className="row mx-0 mt-4">
                        <span className=" font-weight-bold fontS18">Dettagli</span>
                        <hr className="col-12"/>
                        <div className="col-12">
                            <p className="fontS14 mb-2">
                                Nome
                            </p>
                            <input type="text"
                                value={this.state.category.nome}
                                onChange={(e)=>this.setState({category:{...this.state.category,nome: e.target.value}})}
                                className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                            />
                        </div>
                        <div className="col-12">
                            <p className="fontS14 mb-2">
                                Descrizione
                            </p>
                            <textarea type="text"
                                value={this.state.category.descrizione}
                                onChange={(e)=>this.setState({category:{...this.state.category,descrizione: e.target.value}})}
                                className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                            />
                        </div>
                        <div className="col-3">
                            <p className="fontS14 mb-2">
                                Abilitato
                            </p>
                            <select
                                value={this.state.category.abilitato}
                                onChange={(e)=>this.setState({category:{...this.state.category,abilitato: e.target.value}})}
                                className="h48 rounded fontS14 textBlueDeep"
                                >
                                <option value={true}>Abilitata</option>
                                <option value={false}>Disabilitata</option>
                            </select>
                        </div>
                        <div className="col-6"></div>
                        <div className="col-3">
                            <button className="btn btn-success h48 w-100" onClick={this.sendData}>
                                Salva
                            </button>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}
