import React from 'react'
import autoBind from "react-autobind"
import Navbar from "./Navbar"
import { Link } from "react-router-dom"
import firebase from "./Firestone"


export default class Courses extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            selectAll: 0,
            courses: []
        }
        this.getCourses()
        autoBind(this)
    }
    async getCourses(){
        let courses = []
        const db = firebase.firestore();
        await db.collection("corsi").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                courses.push({id: doc.id, selezionato:false, ...doc.data()})
            })
        });
        this.setState({courses})
        console.log(courses)
    }
    render(){
        return(
            <>
            <Navbar />
            <div className="container">
                <div className="w-100 row">
                    <div className="col-9">
                        <div className="h1 my-4 float-left">
                            Corsi
                        </div>
                    </div>
                    <div className="col-3 px-0">
                        <Link to="/courses/update/0">
                            <button className="btn btn-success h48 wFit mt-4">
                                Nuovo Corso
                            </button>
                        </Link>
                    </div>
                </div>
                <div className="row font-weight-bold mt-4">
                    <hr className="col-12"/>
                    <div className="col-1">
                        SKU
                    </div>
                    <div className="col">
                        Nome
                    </div>
                    <div className="col-2 pl-4">
                        Prezzo
                    </div>
                    <div className="col">
                        Abilitato
                    </div>
                    <div className="col">
                        
                    </div>
                    <hr className="col-12 mt-1"/>
                </div>
                {
                    this.state.courses.length
                        ?<Row courses={this.state.courses}/>
                        :"Caricamento..."
                }
            </div>
            </>
        )
    }
}

class Row extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            courses: this.props.courses
        }
        autoBind(this)
    }
    async getCourses(){
        let courses = []
        const db = firebase.firestore();
        await db.collection("corsi").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                courses.push({id: doc.id, selezionato:false, ...doc.data()})
            })
        });
        this.setState({courses})
        console.log(courses)
    }
    deleteCourse(id){
        const db = firebase.firestore();
        db.collection("corsi").doc(id).delete().then(function() {
            alert("Corso Rimosso!")
        }).catch(function(error) {
            alert("Errore: ", error)
        })
        this.getCourses()
    }
    render(){
        return(
            <>
                {
                    this.state.courses.map((course,i)=>{
                        return <div className="row" key={"course-"+i}>
                            <div className="col-1">
                                {course.sku}
                            </div>
                            <div className="col">
                                {course.nome}
                            </div>
                            <div className="col-2 pl-4">
                                {course.prezzo}
                            </div>
                            <div className="col text-center text-white pl-1">
                                {course.abilitato === 'false'
                                    ?<div className="bg-danger rounded wFit px-2">
                                        No
                                    </div>
                                    :<div className="bg-success rounded wFit px-2">
                                        Si
                                    </div>
                                }
                            </div>
                            <div className="col row">
                                <Link to={"/courses/update/"+course.id} className="col bg-warning textWhite h30 rounded-left borderMedium shadowInsetBottom pointer text-center">
                                    Modifica
                                </Link>
                                <Link to={"/modules/"+course.id} className="col bg-white h30 borderMedium shadowInsetBottom pointer text-center">
                                    Moduli
                                </Link>
                                <div className="col bg-danger textWhite h30 rounded-right borderMedium shadowInsetBottom pointer text-center" onClick={()=>this.deleteCourse(course.id)}>
                                    Elimina
                                </div>
                            </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </>
        )
    }
}
