import React from 'react'
import autoBind from "react-autobind"
import { Link } from "react-router-dom";
import Navbar from "./Navbar"
import firebase from "./Firestone"


export default class CourseModules extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            id_course: this.props.match.params.id_course,
            modules: [],
            course: []
        }
        this.getModules()
        this.getCouseData(this.props.match.params.id_course)
        autoBind(this)
    }
    async getModules(){
        let modules = []
        const db = firebase.firestore();
        await db.collection("moduli").where("course", "==", this.state.id_course).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                modules.push({id: doc.id, ...doc.data()})
            });
        console.log(modules)
        });
        this.setState({modules})
    }
    async getCouseData(id){
        console.log('get')
        const db = firebase.firestore();
        let course = null
        await db.collection("corsi").doc(id).get().then((querySnapshot) => {
            course = {...querySnapshot.data()}
        });
        this.setState({course: {...this.state.course, ...course}})
        console.log(this.state.course)
    }
    deleteModule(id){
        const db = firebase.firestore();
        db.collection("moduli").doc(id).delete().then(function() {
            alert("Modulo Rimosso!")
        }).catch(function(error) {
            alert("Errore: ", error)
        })
        this.getModules()
    }
    render(){
        return(
            <>
            <Navbar />
            <div className="container">
                <div className="w-100 row">
                    <div className="col-12">
                        <div className="h1 my-4 float-left">
                            Moduli - {this.state.course.nome}
                        </div>
                    </div>
                </div>
                <div className="row font-weight-bold mt-4">
                    <hr className="col-12"/>
                    <div className="col-2 text-center">
                        Ordine
                    </div>
                    <div className="col">
                        Nome
                    </div>
                    <div className="col-2">
                        File
                    </div>
                    <div className="col">
                        Tipo
                    </div>
                    <div className="col">
                        Abilitato
                    </div>
                    <div className="col">
                        <Link to={"/modules/"+this.state.id_course+"/create/0"} className="bg-success wFit textWhite h30 rounded borderMedium shadowInsetBottom pointer text-center float-right px-3">
                            Aggiungi
                        </Link>
                    </div>
                    <hr className="col-12 mt-1"/>
                </div>
                {
                    this.state.modules.length
                        ?<Row modules={this.state.modules} id_course={this.state.id_course}/>
                        :"Caricamento..."
                }
            </div>
            </>
        )
    }
}

class Row extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            modules: this.props.modules,
            id_course: this.props.id_course
        }
        autoBind(this)
    }
    orderModules(i, direction){
        console.log(i, direction, i+direction)
        let modules = this.state.modules
        if(i+direction<modules.length && i+direction>=0 ){
            let temp = modules[i+direction]
            modules[i+direction] = modules[i]
            modules[i] = temp
            this.setState({modules: modules})
        }
    }
    async getModules(){
        let modules = []
        const db = firebase.firestore();
        await db.collection("moduli").where("course", "==", this.state.id_course).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                modules.push({id: doc.id, ...doc.data()})
            });
        console.log(modules)
        });
        this.setState({modules})
    }
    deleteModule(id){
        console.log(id)
        const db = firebase.firestore();
        db.collection("moduli").doc(id).delete().then(function() {
            alert("Modulo Rimosso!")
        }).catch(function(error) {
            alert("Errore: ", error)
        })
        this.getModules()
    }
    render(){
        return(
            <>
                {
                    this.state.modules.map((module,i)=>{
                        return <div className="row mx-0" key={"module-"+i}>
                            <div className="col-2 text-center row pl-3">
                                <div className="col px-1">
                                    <img src={require('../App/img/icon/arrowDownBlueLite.png')} className="w30 pointer float-right" alt="" onClick={()=>{this.orderModules(i,1)}}/>
                                </div>
                                <div className="col px-1">
                                    <img src={require('../App/img/icon/arrowUpBlueLite.png')} className="w30 pointer float-left" alt="" onClick={()=>{this.orderModules(i,-1)}}/>
                                </div>
                            </div>
                            <div className="col pl-4">
                                {module.nome}
                            </div>
                            <div className="col-2">
                                {module.nome_risorsa}
                            </div>
                            <div className="col">
                                {module.tipo}
                            </div>
                            <div className="col text-center text-white pl-1">
                                {module.abilitato==='false'
                                    ?<div className="bg-danger rounded wFit px-2">
                                        No
                                    </div>
                                    :<div className="bg-success rounded wFit px-2">
                                        Si
                                    </div>
                                }
                            </div>
                            <div className="col row">
                                <Link to={"/modules/"+this.state.id_course+"/update/"+module.id} className="col bg-warning textWhite h30 rounded-left borderMedium shadowInsetBottom pointer text-center">
                                    Modifica
                                </Link>
                                <div className="col bg-danger textWhite h30 rounded-right borderMedium shadowInsetBottom pointer text-center" onClick={()=>this.deleteModule(module.id)}>
                                    Elimina
                                </div>
                            </div>
                            <hr className="col-12"/>
                        </div>
                    })
                }
            </>
        )
    }
}
